                                        <p>Do you want to be a part of colorful fairs and festivals in Mysore? If yes, you can visit this dazzling city during Ugadi or Dasara festival to be a part of the much awaited celebration that takes place every year in Mysore. These festivals and other cultural events represent the traditional legacy of previous time; where people participate in these events with complete devotion and belief. Some of the popular festivals that take place in the Mysore city are Dasara festival, Vairamudi festival, Ugadi festival, and Dusshera.</p>
