import React from 'react';
import { Link } from 'react-router-dom';

function Navbar() {
    return (
        <>
            <div className="container-fluid">
                <div className="row">
                    <div className="col-10 mx-auto">
                        <nav className="navbar navbar-expand-lg navbar-light bg-light">
                            <Link className="navbar-brand brand" href="#">Mysuru<i className='fab fa-typo3 fa-2x' /></Link>
                            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                                <span className="navbar-toggler-icon"></span>
                            </button>

                            <div className="collapse navbar-collapse" id="navbarSupportedContent">
                                <ul className="navbar-nav ml-auto">
                                    <li className="nav-item active mr-3">
                                        <Link to="/</li>" class="nav-link" href="#">Home <span class="sr-only">(current)</span></Link>
                                    </li>
                                    <li className="nav-item mr-3">
                                        <Link to="/About" class="nav-link" href="#">Mysore</Link>
                                    </li>
                                    <li className="nav-item mr-3">
                                        <Link class="nav-link" to="/activities">Activities</Link>
                                    </li>
                                    <li className="nav-item mr-3">
                                        <Link class="nav-link" to="/contact">Contact</Link>
                                    </li>






                                </ul>




                            </div>
                        </nav>

                    </div>
                </div>
            </div>
        </>
    )
}

export default Navbar
