import React from 'react'



function Contact() {
    return (
        <>

            <div class="contact container-fluid contact_header mt-5">
                <div class="row mt-5">
                    <div class="col-md-10 col-12 mx-auto">

                        <div class="row">
                            <div class="col-md-5 pt-3 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column mt-5" id="header_text" >
                                <h1 class="contact_heading">Contact Us</h1>
                                <form className="">
                                    <div class="form-group">
                                        <label for="exampleInputEmail1">Email address</label>
                                        <input type="email" class="form-control" id="exampleInputEmail1"
                                            aria-describedby="emailHelp" placeholder="Enter email" />
                                        <small id="emailHelp" class="form-text text-muted">We'll never share your email with
                                    anyone else.</small>
                                    </div>
                                    <div class="form-group">
                                        <label for="exampleInputPassword1">Password</label>
                                        <input type="password" class="form-control" id="exampleInputPassword1"
                                            placeholder="Password" />
                                    </div>
                                    <div class="form-check">
                                        <input type="checkbox" class="form-check-input" id="exampleCheck1" />
                                        <label class="form-check-label" for="exampleCheck1">Check me out</label>
                                    </div>
                                    <button type="submit" class="btn btn-primary mt-3 mb-5">Submit</button>
                                </form>
                            </div>



                            <div class="col-sm-5 mt-5 text-center">
                                <ul class="list-unstyles">
                                    <li>
                                        <i class="fas fa-map-marker-alt fa-2x i-colo"></i>
                                        <p>Mysuru,Karnataka</p>
                                    </li>
                                    <li>
                                        <i class="fas fa-phone mt-4 fa-2x i-colo"></i>
                                        <p>91-6396328909</p>
                                    </li>
                                    <li>
                                        <i class="fas fa-envelope mt-4 fa-2x i-colo"></i>
                                        <p>mysurutourism32@gmail.com</p>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>


        </>
    )
}

export default Contact
