import React from 'react'
import mysuru1 from "../images/mysuru1.jpg"
import About from "../components/About"
import Activities from './Activities'
import Contact from "./Contact"
import Footer from "./Footer"
import Covid from "./Covid"
function Home() {
    return (
        <>
            <section id="home_main" className="d-flex align-items-center ">
                <div className="container-fluid nav_bg">
                    <div className="row">
                        <div className="col-10 mx-auto">
                            <div className="row">
                                <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column mt-5" id="header_text">
                                    <h1 className="main_header">
                                        Explore <strong className="font-weight-bold" id="mysuru">Mysuru</strong>
                                    </h1>
                                    <h2>The City of Places And Wonders</h2>
                                    <div className="mt-3">
                                        <a className="btn btn-outline-primary btn-lg" href="/About">Start Exploring
                       </a>
                                    </div>

                                </div>
                                <div className="col-lg-6 order-1 order-lg-2 header-img">
                                    <img src={mysuru1} className="img-fluid animated" alt="home.jpg"></img>
                                </div>
                            </div>
                        </div>


                    </div>
                </div>

            </section>
            <About />
            <Activities />
            <Covid />
            <Contact />
            <Footer />







        </>

    )
}

export default Home

