import React from 'react'
import doctor from "../images/doctor-woman.svg"

function Covid() {
    return (
        <>
            <section id="covid_main" className="d-flex align-items-center  ">
                <div className="container-fluid nav_bg">
                    <div className="row">
                        <div className="col-10 mx-auto">

                            <div className="row outer_div">

                                <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column mt-5" id="covid_headeR_text">
                                    <h1 class="text-dark section2heading">How to Stay Safe <br></br><span class="text-primary inside_heading ">While Travelling</span></h1>
                                    <img src={doctor} class="section2img  img-fluid animated" alt="doctor" />
                                </div>










                                <div className="col-lg-6 order-1 order-lg-2 header-img">
                                    <ul class="ulit">
                                        <li><span class="numli">01</span><p> In a post-COVID-19 scenario, it is crucial to stay informed and take adeqaute measures to stay safe, and avoid catching the virus. The risk of infection on an airplane, according to the WHO is actually quite low. Ventilation systems on modern planes provide a total change in air 20-30 times an hour.</p></li><hr></hr>

                                        <li><span class="numli">02</span><p>As a general rule, there are a few items that you should carry with you while travelling. Your list should include a hand sanitiser (with more than 60% alcohol content), disinfectant wipes, tissues, and a face mask. These items can help you maintain a certain level of hygiene and prevent contact with disease-causing respiratory droplets.</p></li><hr></hr>
                                        <li><span class="numli">03</span><p>It’s important to be mindful when using devices or touching surfaces that others have used too, such as ATMs, check-in machines, escalators and the like. Use a generous amount of sanitiser whenever you touch any of these surfaces and be careful not to touch your face.</p></li><hr></hr>
                                        <li><span class="numli">04</span><p>In terms of food and retail outlets within the airport, try not to touch common surfaces, maintain the recommended six-foot distance from the staff, and try to pay using contactless options.
</p></li><hr></hr>
                                    </ul>
                                </div>














                            </div>
                        </div>
                    </div>
                </div>
            </section>









        </>
    )
}

export default Covid;
